import CumulioDashboard from '../../cumulio-dashboard';
import { IState } from '../cumulio.reducer';

const setStatus = (state: IState, { dashboard, status }: { dashboard: CumulioDashboard; status: string }): IState => {
  dashboard.status = status;

  return {
    ...state,
    filters: [],
    dashboards: state.dashboards.map(existingDashboard => {
      if (existingDashboard.dashboardId === dashboard.dashboardId) {
        return dashboard;
      }

      return existingDashboard;
    }),
  };
};

export default setStatus;
