import { IState } from '../cumulio.reducer';

const checkBrowserSupport = (state: IState): IState => {
  const browsercheck = { svg: false, flexbox: false, postMessage: false };

  browsercheck.svg = !!(
    'createElementNS' in document && document.createElementNS('http://www.w3.org/2000/svg', 'svg').createSVGRect
  );

  const doc = document.body || document.documentElement;
  const style = doc.style;
  browsercheck.flexbox = !!('flexWrap' in style) || 'WebkitFlexWrap' in style || 'msFlexWrap' in style;

  browsercheck.postMessage = !!window.postMessage;

  const errorMessage = ['svg', 'flexbox', 'postMessage'].reduce((message, functionality) => {
    if (!browsercheck[functionality]) {
      message += 'Your browser does not support: ' + functionality + '\n';
    }

    return message;
  }, '');

  if (!errorMessage || state.debug) {
    return state;
  }

  return {
    ...state,
    error: {
      message: errorMessage,
      displayableMessage: 'Browser not supported',
    },
  };
};

export default checkBrowserSupport;
