import { ILoadPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import calculateDashboardDimensions from '../epics/calculateDashboardDimensions';

const loadMessage = (state: IState, { dashboardId, dimensions }: ILoadPayload): IState => {
  const dashboard = state.dashboards.find(dashboard => dashboard.dashboardId === dashboardId);

  if (!dimensions) {
    return state;
  }

  try {
    return calculateDashboardDimensions(state, { dashboard, currentScreenmode: state.currentScreenmode, dimensions });
  } catch (err) {
    if (!dashboard) {
      return state;
    }

    dashboard.loaded = true;

    return {
      ...state,
      error: {
        message: err.message,
      },
      dashboards: state.dashboards.map(existingDashboard => {
        if (existingDashboard.dashboardId === dashboard.dashboardId) {
          return dashboard;
        }

        return existingDashboard;
      }),
    };
  }
};

export default loadMessage;
