import uuid from 'uuid/v4';
import { IPublicProps, ScreenMode } from './cumulio-model';
import generateIframeUrl from './utils/generateIframeUrl';

export default class CumulioDashboard {
  readonly appServer?: string = 'https://app.cumul.io/';
  readonly host?: string = 'https://api.cumul.io';
  readonly port?: string = '443';
  readonly version?: string = '0.1.0';

  public _id?: string;
  public dashboardId?: string;
  public container?: string;
  public authKey?: string;
  public authToken?: string;
  public language?: string = 'auto';
  public screenmode?: ScreenMode;
  public switchScreenmodeOnResize?: boolean;
  public chartId?: string;
  public status?: string;
  public chartDimensions?: {
    width?: string;
    height?: string;
  } = {
      width: 'auto',
      height: 'auto',
    };
  public dimensions?: any;
  public iframeDimensions?: any;
  public listeners?: any[];
  public loaded: boolean = false;
  public loader?: {
    background?: string;
    fontColor?: string;
    spinnerColor?: string;
    spinnerBackground?: string;
    logoColor?: string;
  } = {
      background: '#f9f9f9',
      fontColor: '#5a5a5a',
      spinnerColor: 'rgba(255, 165, 0, 0.7)',
      spinnerBackground: 'rgba(169, 169, 169, 0.14)',
      logoColor: 'orange',
    };

  constructor(dashboardId: string, container: string) {
    this._id = uuid();
    this.dashboardId = dashboardId;
    this.container = container;
  }

  extendWithComponentProps(props: IPublicProps) {
    this.loader = {
      background: props.loaderBackground ? props.loaderBackground : this.loader.background,
      fontColor: props.loaderFontColor ? props.loaderFontColor : this.loader.fontColor,
      spinnerColor: props.loaderSpinnerColor ? props.loaderSpinnerColor : this.loader.spinnerColor,
      spinnerBackground: props.loaderSpinnerBackground ? props.loaderSpinnerBackground : this.loader.spinnerBackground,
      logoColor: props.loaderLogoColor ? props.loaderLogoColor : this.loader.logoColor,
    };

    for (const prop in props) {
      this[prop] = props[prop];
    }
  }

  get chartEmbedding() {
    return this.chartId ? true : false;
  }

  getIframeUrl(currentMode) {
    return generateIframeUrl(this, currentMode);
  }
}
