import { IDashboardFilter, IDashboardParsedFilter } from '../../cumulio-model';
import { IChangeFiltersPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';

const parseFilter = (filter: IDashboardFilter): IDashboardParsedFilter => {
  let parsedFilter: any = {};

  if (filter.parameters) {
    parsedFilter = { columnId: filter.parameters[0].column_id, value: filter.parameters[1] };
  } else {
    parsedFilter = { columnId: filter.column_id, value: filter.value };
  }

  switch (filter.expression) {
    case '? between ?':
      parsedFilter.expression = 'between';
      break;
    case '? > ?':
      parsedFilter.expression = 'greater than';
      break;
    case '? >= ?':
      parsedFilter.expression = 'greater than or equal';
      break;
    case '? <= ?':
      parsedFilter.expression = 'smaller than or equal';
      break;
    case '? < ?':
      parsedFilter.expression = 'smaller than';
      break;
    case '? in ?':
      parsedFilter.expression = 'is in';
      break;
    case '? = ?':
      parsedFilter.expression = 'equals';
      break;
    default:
      parsedFilter.expression = 'equals';
  }

  return parsedFilter;
};

const parseFilters = (filters: IDashboardFilter[]) => {
  return filters.map(filter => parseFilter(filter));
};

const changeFiltersMessage = (state: IState, { dashboardId, filters }: IChangeFiltersPayload): IState => {
  const parsedFilters = parseFilters(filters);

  const newState = Object.assign({}, state);
  const index = newState.filters.findIndex(filter => filter.dashboardId === dashboardId);

  if (filters.length === 0) {
    newState.filters.splice(index, 1);

    return newState;
  }

  if (index > -1) {
    newState.filters[index].activeFilters = parsedFilters;
  } else {
    newState.filters.push({
      activeFilters: parsedFilters,
      dashboardId,
    });
  }

  return newState;
};

export default changeFiltersMessage;
