import CumulioDashboard from '../cumulio-dashboard';
import { IDashboardFilter, ScreenMode } from '../cumulio-model';
import { Actions } from './cumulio.types';

export type Dimensions = {
  [key in ScreenMode]: number[];
};

export interface IValidateDashboardPayload {
  dashboard: CumulioDashboard;
}

export const validateDashboardAction = (payload: IValidateDashboardPayload) => {
  return {
    type: Actions.VALIDATE_DASHBOARD,
    payload,
  };
};

export const checkBrowserAction = () => {
  return {
    type: Actions.CHECK_BROWSER,
  };
};

export interface ICalculateDimensionsPayload {
  dashboard: CumulioDashboard;
  currentScreenmode: string;
  dimensions?: Dimensions;
}

export const calculateDimensionsAction = (payload: ICalculateDimensionsPayload) => {
  return {
    type: Actions.CALCULATE_DIMENSIONS,
    payload,
  };
};

export interface IInitPayload {
  replaceOldDashboards: boolean;
}

export const initAction = (payload: IInitPayload) => {
  return {
    type: Actions.INIT,
    payload,
  };
};

export interface IChangeFiltersPayload {
  dashboardId: string;
  filters: IDashboardFilter[];
}

export const changeFiltersAction = (payload: IChangeFiltersPayload) => {
  return {
    type: Actions.CHANGE_FILTERS,
    payload,
  };
};

export interface IDataPayload {
  rawData: any;
}

export const dataAction = (payload: IDataPayload) => {
  return {
    type: Actions.DATA,
    payload,
  };
};

export interface ILoadPayload {
  dashboardId: string;
  language: string;
  dimensions: Dimensions;
}

export const loadAction = (payload: ILoadPayload) => {
  return {
    type: Actions.LOAD,
    payload,
  };
};

export interface IErrorPayload {
  dashboardId: string;
}

export const errorAction = (payload: IErrorPayload) => {
  return {
    type: Actions.ERROR,
    payload,
  };
};

export interface IExportPayload {
  dashboardId: string;
  error?: {
    msg?: string;
  };
}

export const exportAction = (payload: IExportPayload) => {
  return {
    type: Actions.EXPORT,
    payload,
  };
};

export interface ISetCurrentscreenModePayload {
  mode: string;
}

export const setCurrentscreenModeAction = (payload: ISetCurrentscreenModePayload) => {
  return {
    type: Actions.SET_CURRENT_SCREEN_MODE,
    payload,
  };
};

export interface ISetContainerWidthPayload {
  width: number;
}

export const setContainerWidthAction = (payload: ISetContainerWidthPayload) => {
  return {
    type: Actions.SET_CONTAINER_WIDTH,
    payload,
  };
};

export interface IGetDataPayload {
  chartId: string;
  dashboard?: {
    dashboardId?: string;
    container?: string;
  };
}

export const getDataAction = (payload: IGetDataPayload) => {
  return {
    type: Actions.GET_DATA,
    payload,
  };
};

export interface ISetAuthorizationPayload {
  authKey: string;
  authToken: string;
  dashboard?: {
    dashboardId?: string;
    container?: string;
  };
}

export const setAuthorizationAction = (payload: ISetAuthorizationPayload) => {
  return {
    type: Actions.SET_AUTHORIZATION,
    payload,
  };
};

export interface IRefreshDataPayload {
  id?: string;
}

export const refreshDataAction = (payload: IRefreshDataPayload = {}) => {
  return {
    type: Actions.REFRESH_DATA,
    payload,
  };
};

export interface IExportDashboardPayload {
  container: string;
  format?: string;
}

export const exportDashboardAction = (payload: IExportDashboardPayload) => {
  return {
    type: Actions.EXPORT_DASHBOARD,
    payload,
  };
};

export const reloadDashboardAction = () => {
  return {
    type: Actions.RELOAD_DASHBOARD,
  };
};
