import { breakpoints, IAvailableScreenmode, ScreenMode } from '../../cumulio-model';
import { Dimensions, ICalculateDimensionsPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import setStatus from './setStatus';

const calculateDashboardDimensions = (
  state: IState,
  { dashboard, currentScreenmode, dimensions }: ICalculateDimensionsPayload,
): IState => {
  const { containerWidth } = state;

  if (!dashboard.chartDimensions || !dimensions) {
    throw new Error(`No valid dimensions given for dashboard ${dashboard.dashboardId}`);
  }

  dashboard.dimensions = dimensions;

  let currentWidth, currentHeight, currentMode;

  const calculateScreenModeDimensions = (screenMode: IAvailableScreenmode) => {
    if (
      dashboard.chartEmbedding &&
      dashboard.chartDimensions.width !== 'auto' &&
      dashboard.chartDimensions.height === 'auto'
    ) {
      if (screenMode.width[0] < parseInt(dashboard.chartDimensions.width)) {
        currentMode = screenMode.mode;
        currentWidth = parseInt(dashboard.chartDimensions.width, 10);
        currentHeight = dimensions[currentMode][1];
      }
    } else {
      if (screenMode.width[0] < containerWidth) {
        currentMode = screenMode.mode;
        if (dashboard.chartEmbedding) {
          if (
            dashboard.chartDimensions &&
            dashboard.chartDimensions.width &&
            dashboard.chartDimensions.width !== 'auto'
          ) {
            currentWidth = parseInt(dashboard.chartDimensions.width, 10);
          } else {
            currentWidth = Math.min(containerWidth, screenMode.width[1]);
          }
          if (
            dashboard.chartDimensions &&
            dashboard.chartDimensions.height &&
            dashboard.chartDimensions.height !== 'auto'
          ) {
            currentHeight = parseInt(dashboard.chartDimensions.height, 10);
          } else {
            currentHeight = dimensions[currentMode][1];
          }
          if (
            dashboard.chartDimensions &&
            dashboard.chartDimensions.width !== 'auto' &&
            dashboard.chartDimensions.height === 'auto'
          ) {
            currentWidth = parseInt(dashboard.chartDimensions.width, 10);
            currentHeight = dimensions[currentMode][1];
          }
        } else {
          // Normal dashboard embedding
          currentWidth = Math.max(Math.min(screenMode.width[1], containerWidth), screenMode.width[0]);
          currentHeight = screenMode.height;
        }
      }
    }
  };

  // If screen may not be resized (and currentscreenmode exists) then set dashboard screenmode to currentscreenmode
  // So if switch screenmode on resize is true => screenmode will be 'auto'
  if (!dashboard.switchScreenmodeOnResize && currentScreenmode) {
    dashboard.screenmode = currentScreenmode as ScreenMode;
  }

  let availableScreenModes = [];

  if (Object.keys(dimensions) && Object.keys(dimensions)[0] === ScreenMode.FIXED) {
    currentMode = ScreenMode.FIXED;
    currentWidth = dimensions.fixed[0];
    currentHeight = dimensions.fixed[1];
  } else {
    if (dashboard.chartEmbedding) {
      dimensions = setChartEmbeddingAvailableScreenmodes(dimensions);
    }

    availableScreenModes = setAvailableScreenModes(dimensions);

    // Check if specified is in available -> if not set to auto
    // Check if the screenmode in cumulio exists, if it doesn't exist, set the screenmode to auto
    if (availableScreenModes.map(screenMode => screenMode.mode).indexOf(dashboard.screenmode) === -1) {
      dashboard.screenmode = ScreenMode.AUTO;
    }

    if (dashboard.screenmode === ScreenMode.AUTO) {
      availableScreenModes.forEach(screenmode => {
        calculateScreenModeDimensions(screenmode);
      });
    }
    // Set to screenmode and check if width fits in available width
    else {
      const chosenMode = availableScreenModes.find(screenMode => screenMode.mode === dashboard.screenmode);
      currentMode = dashboard.screenmode;
      currentWidth = Math.max(Math.min(chosenMode.width[1], containerWidth), chosenMode.width[0]);
      currentHeight = chosenMode.height;

      if (dashboard.chartEmbedding) {
        if (
          dashboard.chartDimensions &&
          dashboard.chartDimensions.width &&
          dashboard.chartDimensions.width !== 'auto'
        ) {
          currentWidth = parseInt(dashboard.chartDimensions.width, 10);
        }
        if (
          dashboard.chartDimensions &&
          dashboard.chartDimensions.height &&
          dashboard.chartDimensions.height !== 'auto'
        ) {
          currentHeight = parseInt(dashboard.chartDimensions.height, 10);
        }
      }
    }

    // If no available mode is found, take the lowest one, only when available is not empty
    if (!currentMode && availableScreenModes.length !== 0) {
      currentMode = availableScreenModes[0].mode;
      currentWidth = availableScreenModes[0].width[0];
      currentHeight = availableScreenModes[0].height;
    }
  }

  let iframeHeight = currentHeight + 'px';
  let iframeWidth = currentWidth + 'px';

  // Change again if chartembedding
  if (dashboard.chartEmbedding && dashboard.chartDimensions) {
    iframeWidth = dashboard.chartDimensions.width + 'px';
    iframeHeight = dashboard.chartDimensions.height + 'px';
  }

  dashboard.loaded = true;
  dashboard.iframeDimensions = {
    width: iframeWidth,
    height: iframeHeight,
  };

  state = setStatus(state, { dashboard, status: 'loaded' });

  return {
    ...state,
    currentScreenmode: currentMode,
    dashboards: state.dashboards.map(existingDashboard => {
      if (existingDashboard.dashboardId === dashboard.dashboardId) {
        return dashboard;
      }

      return existingDashboard;
    }),
  };
};

const setChartEmbeddingAvailableScreenmodes = (dimensions: Dimensions) => {
  let firstAvailableScreenMode = Object.keys(dimensions)[0];

  Object.values(ScreenMode).forEach(screenMode => {
    if (!['fixed', 'auto'].includes(screenMode)) {
      const availableScreenMode = screenMode in Object.keys(dimensions);
      if (!availableScreenMode) {
        dimensions[screenMode] = dimensions[firstAvailableScreenMode];
      } else {
        firstAvailableScreenMode = screenMode;
      }
    }
  });

  return dimensions;
};

const setAvailableScreenModes = (dimensions: Dimensions): IAvailableScreenmode[] => {
  return Object.keys(dimensions).map(dimension => {
    let screenmode = { width: [], height: [], mode: dimension };

    if (Object.values(ScreenMode).includes(dimension as ScreenMode)) {
      Object.keys(breakpoints).forEach(breakpoint => {
        if (breakpoint === dimension) {
          screenmode.width = breakpoints[breakpoint];
        }
      });
      screenmode.height = dimensions[dimension][1] || 50;
    }

    return screenmode;
  });
};

export default calculateDashboardDimensions;
