import CumulioDashboard from '../cumulio-dashboard';
import { IDashboardFilters, IError, ScreenMode } from '../cumulio-model';
import { Actions } from './cumulio.types';
import calculateDashboardDimensions from './epics/calculateDashboardDimensions';
import checkBrowserSupport from './epics/checkBrowserSupport';
import getData from './epics/getData';
import refreshData from './epics/refreshData';
import setAuthorization from './epics/setAuthorization';
import validateDashboard from './epics/validateDashboard';
import changeFiltersMessage from './messages/changeFiltersMessage';
import dataMessage from './messages/dataMessage';
import errorMessage from './messages/errorMessage';
import exportMessage from './messages/exportMessage';
import loadMessage from './messages/loadMessage';
import exportDashboard from './epics/exportDashboard';
import reloadDashboard from './epics/reloadDashboard';

export interface IState {
  dashboards?: CumulioDashboard[];
  error?: IError;
  replaceOldDashboards: boolean;
  containerWidth: number;
  currentScreenmode?: ScreenMode;
  filters?: IDashboardFilters[];
  data: object[];
  debug: boolean;
}

export const initialState: IState = {
  dashboards: [],
  filters: [],
  data: [],
  containerWidth: null,
  replaceOldDashboards: false,
  debug: false,
};

export function reducer(state: IState, action): IState {
  try {
    switch (action.type) {
      case Actions.VALIDATE_DASHBOARD:
        return validateDashboard(state, action.payload.dashboard);
      case Actions.CHECK_BROWSER:
        return checkBrowserSupport(state);
      case Actions.SET_CURRENT_SCREEN_MODE:
        return {
          ...state,
          currentScreenmode: action.payload.mode,
        };
      case Actions.SET_CONTAINER_WIDTH:
        return {
          ...state,
          containerWidth: action.payload.width,
        };
      case Actions.INIT:
        return {
          ...state,
          replaceOldDashboards: action.payload.replaceOldDashboards,
        };
      case Actions.CALCULATE_DIMENSIONS:
        return calculateDashboardDimensions(state, action.payload);
      case Actions.CHANGE_FILTERS:
        return changeFiltersMessage(state, action.payload);
      case Actions.DATA:
        return dataMessage(state, action.payload);
      case Actions.LOAD:
        return loadMessage(state, action.payload);
      case Actions.ERROR:
        return errorMessage(state, action.payload);
      case Actions.EXPORT:
        return exportMessage(state, action.payload);
      case Actions.GET_DATA:
        return getData(state, action.payload);
      case Actions.SET_AUTHORIZATION:
        return setAuthorization(state, action.payload);
      case Actions.REFRESH_DATA:
        return refreshData(state, action.payload);
      case Actions.EXPORT_DASHBOARD:
        return exportDashboard(state, action.payload);
      case Actions.RELOAD_DASHBOARD:
        return reloadDashboard(state);
      default:
        throw new Error();
    }
  } catch (err) {
    return {
      ...state,
      error: {
        message: err.message,
      },
    };
  }
}
