import CumulioDashboard from '../cumulio-dashboard';

const generateIframeUrl = (dashboard: CumulioDashboard, currentScreenmode?: string): string => {
  const {
    language,
    loader,
    screenmode,
    authKey,
    authToken,
    chartDimensions,
    version,
    dashboardId,
    chartId,
    chartEmbedding,
  } = dashboard;
  const queryString: any = {};
  const appServer = dashboard.appServer ? dashboard.appServer : '';

  if (language !== 'auto') {
    queryString.language = language;
  }

  if (loader) {
    if (loader.background) {
      queryString.ldrbg = loader.background.replace('#', '--');
    }
    if (loader.fontColor) {
      queryString.ldrftclr = loader.fontColor.replace('#', '--');
    }
    if (loader.spinnerColor) {
      queryString.ldrspclr = loader.spinnerColor.replace('#', '--');
    }
    if (loader.spinnerBackground) {
      queryString.ldrspbg = loader.spinnerBackground.replace('#', '--');
    }
    if (loader.logoColor) {
      queryString.ldrlgclr = loader.logoColor.replace('#', '--');
    }
  }

  const specifiedScreenmode =
    screenmode && ['mobile', 'tablet', 'desktop', 'largescreen', 'fixed'].indexOf(screenmode) > -1;
  if (specifiedScreenmode && screenmode === currentScreenmode) {
    queryString.screenmode = currentScreenmode;
  }

  if (authKey && authToken) {
    queryString.key = authKey;
    queryString.token = authToken;
  }

  if (chartEmbedding && chartDimensions) {
    if (chartDimensions.width) {
      queryString.width = chartDimensions.width;
    }
    if (chartDimensions.height) {
      queryString.height = chartDimensions.height;
    }
  }

  queryString.version = `react${version}`;

  const joinedQueryString = Object.keys(queryString)
    .map(key => {
      return key + '=' + encodeURIComponent(queryString[key]);
    })
    .join('&');

  let iframeUrl = '';
  if (chartEmbedding) {
    iframeUrl =
      appServer + 'i/' + dashboardId + '/' + chartId + (joinedQueryString.length === 0 ? '' : '?' + joinedQueryString);
  } else {
    iframeUrl = appServer + 'i/' + dashboardId + (joinedQueryString.length === 0 ? '' : '?' + joinedQueryString);
  }

  return sanitizeUrl(iframeUrl);
};

const sanitizeUrl = (url: string) => {
  const invalidPrototcolRegex = /^(%20|\s)*(javascript|data)/im;
  const ctrlCharactersRegex = /[^\x20-\x7E]/gim;
  const urlSchemeRegex = /^([^:]+):/gm;

  if (!url) {
    return 'about:blank';
  }

  const sanitizedUrl = url.replace(ctrlCharactersRegex, '').trim();
  if (!isAbsoluteUrl(sanitizedUrl)) {
    return sanitizedUrl;
  }

  const urlSchemeParseResults = sanitizedUrl.match(urlSchemeRegex);
  if (!urlSchemeParseResults) {
    return sanitizedUrl;
  }

  const urlScheme = urlSchemeParseResults[0];
  if (invalidPrototcolRegex.test(urlScheme)) {
    return 'about:blank';
  }

  return sanitizedUrl;
};

const isAbsoluteUrl = (url: string) => {
  const r = new RegExp('^(?:[a-z]+:)?//', 'i');

  return r.test(url);
};

export default generateIframeUrl;
