import { createContext, Dispatch, useContext } from 'react';
import { IState } from '../cumulio.reducer';

export const CumulioContext = createContext(null);

interface IContext {
  state: IState;
  dispatch: Dispatch<any>;
}

export function useCumulio(): IContext {
  const context = useContext(CumulioContext);

  if (!context) {
    throw new Error('useCumulio must be used within a CumulioProvider');
  }

  return context;
}
