import { IExportDashboardPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import getIframesForDashboard from '../utils/getIframesForDashboard';

const exportDashboard = (state: IState, { container, format = "png" }: IExportDashboardPayload): IState => {
  const iframes = getIframesForDashboard({ container });

  if (iframes.length === 0) {
    console.warn('Export error: Could not retrieve the dashboard or the container.');
  }

  iframes.forEach(iframe => {
    const dashboard = state.dashboards.find(dashboard => iframe.src.includes(dashboard.dashboardId));
    iframe.contentWindow.postMessage({ action: 'exportDashboard', format }, '*');

    if (!dashboard) {
      console.log('Export error: Wait until dashboard is rendered.');
    } else {
      let msg = 'Exporting dashboard ';

      if (dashboard.dashboardId) msg = msg + dashboard.dashboardId;
      if (dashboard.chartId) msg = msg + ' - chart: ' + dashboard.chartId;
      if (dashboard.container) msg = msg + ' in container ' + dashboard.container;

      console.log(msg);
    }
  });

  return state;
};

export default exportDashboard;
