import { useWindowSize } from '@react-hook/window-size';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from "react";
import uuid from 'uuid/v4';
import "./cumulio-component.scss";
import CumulioDashboard from './cumulio-dashboard';
import { breakpoints, ICssStyles, IError, IPublicProps, ScreenMode, Translations } from './cumulio-model';
import { calculateDimensionsAction, checkBrowserAction, setContainerWidthAction, setCurrentscreenModeAction, validateDashboardAction } from './store/cumulio.actions';
import { useCumulio } from './store/hooks/useCumulio';
import useMessage from './store/hooks/useMessage';

const CumulioComponent = (props: IPublicProps) => {
  const { switchScreenModeOnResize } = props;

  // Refs
  const containerRef = useRef(null);

  const [windowWidth, windowHeight] = useWindowSize();

  const [iframeSrc, setIframeSrc] = useState(null);
  let cssOptions: ICssStyles = {
    borderWidth: 3,
    loaderSize: 32,
    minHeight: 400,
  };

  useMessage();

  const [loadingMessage, setLoadingMessage] = useState('');

  const { state, dispatch } = useCumulio();

  const containerId = `dashboard-${uuid()}`;
  const paramsId = uuid();
  const iframeId = 'cio-dashboard-' + paramsId;

  let dashboard = createInitialDashboard(props, containerId);

  const { error, dashboards } = state;
  const validatedDashboard = dashboards.find(d => d.dashboardId === dashboard.dashboardId);
  if (validatedDashboard) {
    dashboard = validatedDashboard;
  }

  const { chartEmbedding, loader, loaded, chartDimensions } = dashboard;

  useEffect(() => {
    dispatch(checkBrowserAction());

    dispatch(validateDashboardAction({ dashboard }));

    buildLoadingMessage();

    setIframeSrc(dashboard.getIframeUrl(state.currentScreenmode));

    dispatch(setContainerWidthAction({ width: containerRef.current.offsetWidth }));

    getCurrentScreenmode();

  }, [props.dashboardId]);

  // Dimensions should not be calculated on initial width, it's done in the load event.
  const isFirstRun = useRef(true);

  useEffect(() => {
    dispatch(setContainerWidthAction({ width: containerRef.current.offsetWidth }));

    if (switchScreenModeOnResize) {
      getCurrentScreenmode();
    }

    if (isFirstRun.current) {
      isFirstRun.current = false;
      return;
    }

    dispatch(calculateDimensionsAction({ dashboard, dimensions: dashboard.dimensions, currentScreenmode: state.currentScreenmode }));

  }, [windowWidth, windowHeight]);

  useEffect(() => {
    if (dashboard.iframeDimensions !== undefined) {
      const dashboardElements = document.getElementsByClassName('cio-embed-dashboard') as any;
      for (const iframe of dashboardElements) {
        if (iframe.src.includes(dashboard.dashboardId)) {
          iframe.style.height = dashboard.iframeDimensions.height;
          iframe.style.width = dashboard.iframeDimensions.width;
        }
      }
    }

  }, [dashboard.iframeDimensions]);

  if (error) {
    handleError(error, dashboard);
  }

  // Initial screenmode -> set currentscreenmode
  const getCurrentScreenmode = () => {
    Object.keys(breakpoints).forEach(screenmode => {
      const breakpoint = breakpoints[screenmode];
      if (breakpoint[0] <= windowWidth && windowWidth <= breakpoint[1]) {
        dispatch(setCurrentscreenModeAction({ mode: screenmode }));
      }
    });
  }

  const buildLoadingMessage = () => {
    let msg = "";

    if (dashboard.language in Object.keys(Translations.initializing)) {
      msg = Translations.initializing[dashboard.language];
    } else {
      msg = Translations.initializing.en;
    }

    setLoadingMessage(msg);
  }

  const backgroundStyles = () => {
    if (loader.background) {
      return {
        background: dashboard.loader.background
      };
    }
  }

  const containerLoaderStyles = () => {
    const chartHeightDefined = chartEmbedding && chartDimensions.height !== 'auto';

    return {
      display: loaded ? "none" : "",
      height: (chartHeightDefined ? chartDimensions.height : cssOptions.minHeight) + 'px',
    };
  }

  const loaderStyles = () => {
    if (chartEmbedding) {
      cssOptions = {
        borderWidth: 4,
        loaderSize: 24,
        minHeight: 200
      };

      const chartWidthDefined = chartDimensions.width !== 'auto';
      const chartHeightDefined = chartDimensions.height !== 'auto';

      return {
        background: loader.background,
        height: (chartHeightDefined ? chartDimensions.height : '200') + 'px',
        minHeight: chartHeightDefined ? '' : '200px',
        width: chartWidthDefined ? chartDimensions.width + 'px;' : '100%',
      };
    }

    return {
      background: loader.background
    };
  }

  const ciolcStyles = () => {
    const { chartId } = dashboard;
    const chartHeightDefined = chartEmbedding && chartDimensions.height !== 'auto';

    let padding = 16;
    if (chartId) {
      if (chartHeightDefined) {
        padding += (parseInt(chartDimensions.height, 10) - cssOptions.loaderSize - 32) / 2;
      }
      else {
        padding += (cssOptions.minHeight - cssOptions.loaderSize - 32) / 2;
      }
    } else {
      padding += 128;
    }

    return {
      padding: `${padding}px`
    }
  }

  const cioplStyles = () => {
    return {
      borderBottom: cssOptions.borderWidth + 'px solid ' + loader.spinnerBackground,
      borderLeft: cssOptions.borderWidth + 'px solid ' + loader.spinnerBackground,
      borderRight: cssOptions.borderWidth + 'px solid ' + loader.spinnerColor,
      borderTop: cssOptions.borderWidth + 'px solid ' + loader.spinnerColor,
      height: cssOptions.loaderSize + 'px',
      width: cssOptions.loaderSize + 'px',
    };
  }

  const ciolmStyles = () => {
    return {
      color: loader.fontColor
    }
  }

  const iframeStyles = () => {
    const styleObject: any = { opacity: 0, height: '400px' };
    if (chartEmbedding) {
      if (chartDimensions.height !== 'auto') {
        styleObject.height = chartDimensions.height + 'px';
      }
      else {
        styleObject.height = '200px';
      }

      // Standard Width
      if (chartDimensions.width !== 'auto') {
        styleObject.width = chartDimensions.width + 'px';
      }
      else {
        styleObject.width = '100%';
      }
    }

    // If status loaded, show iframe
    if (loaded) {
      styleObject.opacity = 1;
    }

    if (error) {
      styleObject.width = '100%';
      styleObject.height = '400px';
    }

    return styleObject;
  }

  return (
    <>
      <div id={containerId} className="dashboard-container" style={backgroundStyles()} ref={containerRef}>
        {!error && (
          <div className="cio-container-loader" id={'cio-container-loader-' + paramsId} style={containerLoaderStyles()}>
            <div className="cio-loader" id={'cio-loader-' + paramsId} style={loaderStyles()}>
              <div className="cio-lc" style={ciolcStyles()}>
                <div className={'cio-pl' + (chartEmbedding ? ' chartEmbedding' : '')} style={cioplStyles()}></div>
                {!chartEmbedding && (<div className="cio-lm" style={ciolmStyles()}>{loadingMessage}</div>)}
              </div>
            </div>
          </div>
        )}
        <iframe
          className="cio-embed-dashboard"
          id={iframeId}
          name={iframeId}
          src={iframeSrc}
          style={iframeStyles()}
        />
      </div>
      {error && error.displayableMessage && (<div id={'cio-error-' + paramsId}><h3 className="cio-error">{error.displayableMessage}</h3></div>)}
    </>
  );
};

const createInitialDashboard = (props: IPublicProps, containerId: string): CumulioDashboard => {
  const dashboard = new CumulioDashboard(props.dashboardId, `#${containerId}`);
  dashboard.extendWithComponentProps(props);

  return dashboard;
}

const handleError = (error: IError, dashboard: CumulioDashboard) => {
  let msg = 'The dashboard encountered an error';
  if (dashboard.dashboardId === 'string') { msg += ' for dashboard with id ' + dashboard.dashboardId; }
  if (dashboard.container === 'string') { msg += ' in container ' + dashboard.container; }
  if (error && error.message) {
    msg += ':\n' + error.message;
  }

  error && console.error(msg);
}

CumulioComponent.defaultProps = {
  language: "auto",
  screenmode: ScreenMode.AUTO,
  switchScreenmodeOnResize: true
};

CumulioComponent.propTypes = {
  screenmode: PropTypes.oneOf(Object.values(ScreenMode)),
};

export default CumulioComponent;
