import { ILoadPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import setStatus from '../epics/setStatus';

const errorMessage = (state: IState, { dashboardId }: ILoadPayload): IState => {
  const dashboard = state.dashboards.find(dashboard => dashboard.dashboardId === dashboardId);

  state = setStatus(state, { dashboard, status: 'error' });

  return state;
};

export default errorMessage;
