import useEventListener from '@use-it/event-listener';
import { MessageTypes } from '../../cumulio-model';
import { changeFiltersAction, dataAction, errorAction, exportAction, initAction, loadAction } from '../cumulio.actions';
import { useCumulio } from './useCumulio';

const useMessage = () => {
  const { state, dispatch } = useCumulio();

  useEventListener('message', (message: any) => {
    const { data } = message;

    if (!data) {
      return;
    }

    switch (data.type) {
      case MessageTypes.INIT:
        dispatch(initAction({ replaceOldDashboards: true }));
        break;
      case MessageTypes.CHANGED_FILTERS:
        const filters = data.filters.filter(
          activeFilter => ['itemFilter', 'global'].indexOf(activeFilter.properties.origin) === -1,
        );
        dispatch(changeFiltersAction({ dashboardId: data.dashboard, filters }));
        break;
      case MessageTypes.DATA:
        dispatch(dataAction({ rawData: data.data }));
        break;
      case MessageTypes.LOAD:
        dispatch(loadAction({ dashboardId: data.dashboard, dimensions: data.dimensions, language: data.language }));
        break;
      case MessageTypes.ERROR:
        dispatch(loadAction({ dashboardId: data.dashboard, dimensions: data.dimensions, language: data.language }));
        dispatch(errorAction({ dashboardId: data.dashboard }));
        break;
      case MessageTypes.EXPORT:
        dispatch(loadAction({ dashboardId: data.dashboard, dimensions: data.dimensions, language: data.language }));
        dispatch(exportAction({ dashboardId: data.dashboard, error: data.error ? data.error : null }));
        break;
      default:
        break;
    }
  });
};

export default useMessage;
