import CumulioDashboard from '../../cumulio-dashboard';
import { IState } from '../cumulio.reducer';

const validateDashboard = (state: IState, dashboard: CumulioDashboard): IState => {
  const cleanedDashboard = cleanUpParams(dashboard);

  let { screenmode, chartDimensions, switchScreenmodeOnResize } = cleanedDashboard;

  if (screenmode && screenmode !== 'auto') {
    switchScreenmodeOnResize = false;
  }
  if (chartDimensions.width !== 'auto') {
    chartDimensions.width = (chartDimensions.width + '').replace('px', '') || 'auto';
  }
  if (chartDimensions.height !== 'auto') {
    chartDimensions.height = (chartDimensions.height + '').replace('px', '') || 'auto';
  }

  const newState = Object.assign({}, state);
  let dashboardExists = false;
  newState.dashboards &&
    newState.dashboards.forEach(d => {
      // Dashboard exists -- Update Dashboard
      if (cleanedDashboard.dashboardId === d.dashboardId) {
        d = cleanedDashboard;
        dashboardExists = true;
      }
    });

  if (!dashboardExists) {
    if (newState.replaceOldDashboards === true) {
      newState.dashboards = [];
      newState.replaceOldDashboards = false;
    }

    newState.dashboards.push(cleanedDashboard);
  }

  return newState;
};

const cleanUpParams = (properties): CumulioDashboard => {
  for (var prop in properties) {
    if (properties[prop] === null || properties[prop] === undefined) {
      delete properties[prop];
    }
    if (typeof properties[prop] === 'object') {
      properties[prop] = cleanUpParams(properties[prop]);
    }
  }

  return properties;
};

export default validateDashboard;
