import { IRefreshDataPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';

const refreshData = (state: IState, { id }: IRefreshDataPayload): IState => {
  let dashboards: any = document.getElementsByClassName('cio-embed-dashboard');

  for (const iframe of dashboards) {
    iframe.contentWindow.postMessage({ action: 'refreshData', id: id ? id : null }, '*');
  }
  return state;
};

export default refreshData;
