const getIframesForDashboard = dashboard => {
  const iframes = [];
  let dashboards: any = document.getElementsByClassName('cio-embed-dashboard');

  if (dashboard.container) {
    const container: HTMLElement = document.querySelector('.' + dashboard.container);

    if (!container) {
      throw new Error(`Container cannot be found with class selector ${dashboard.container}`);
    }

    const children: any = container.children;
    for (const child of children) {
      dashboards = child.getElementsByClassName('cio-embed-dashboard');
      for (const iframe of dashboards) {
        if (dashboard.dashboardId) {
          if (iframe.src.includes(dashboard.dashboardId)) {
            iframes.push(iframe);
          }
        } else {
          iframes.push(iframe);
        }
      }
    }
  } else {
    for (const iframe of dashboards) {
      if (dashboard.dashboardId) {
        if (iframe && iframe.src.includes(dashboard.dashboardId)) {
          iframes.push(iframe);
        }
      } else {
        iframes.push(iframe);
      }
    }
  }

  return iframes;
};

export default getIframesForDashboard;
