import { IDataPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';

const dataMessage = (state: IState, { rawData }: IDataPayload): IState => {
  const data: any[] = [];

  rawData.forEach(row => {
    const processedRow: any[] = [];
    row.forEach(value => {
      if (typeof value === 'object') {
        processedRow.push(value.id);
      } else {
        processedRow.push(value);
      }
    });
    data.push(processedRow);
  });

  return {
    ...state,
    data,
  };
};

export default dataMessage;
