import { IExportPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import setStatus from '../epics/setStatus';

const exportMessage = (state: IState, { dashboardId, error }: IExportPayload): IState => {
  const dashboard = state.dashboards.find(dashboard => dashboard.dashboardId === dashboardId);

  state = setStatus(state, { dashboard, status: 'export' });

  if (error) {
    console.error(error.msg);
  }

  return state;
};

export default exportMessage;
