import { ISetAuthorizationPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import getIframesForDashboard from '../utils/getIframesForDashboard';

const setAuthorization = (state: IState, { authKey, authToken, dashboard }: ISetAuthorizationPayload): IState => {
  const iframes = getIframesForDashboard(dashboard);

  iframes.forEach(iframe => {
    if (dashboard.dashboardId) {
      iframe.contentWindow.postMessage(
        { action: 'setAuthorization', key: authKey, token: authToken, dashboardId: dashboard.dashboardId },
        '*',
      );
    } else {
      iframe.contentWindow.postMessage({ action: 'setAuthorization', key: authKey, token: authToken }, '*');
    }
  });

  return state;
};

export default setAuthorization;
