import { IState } from '../cumulio.reducer';

const reloadDashboard = (state: IState): IState => {
  let dashboards: any = document.getElementsByClassName('cio-embed-dashboard');

  for (const iframe of dashboards) {
    iframe.contentWindow.postMessage({ action: 'refreshData' }, '*');
  }
  return state;
};

export default reloadDashboard;
