import { IGetDataPayload } from '../cumulio.actions';
import { IState } from '../cumulio.reducer';
import getIframesForDashboard from '../utils/getIframesForDashboard';

const getData = (state: IState, { chartId, dashboard }: IGetDataPayload): IState => {
  const iframes = getIframesForDashboard(dashboard);

  iframes.forEach(iframe => {
    iframe.contentWindow.postMessage({ action: 'getData', id: chartId }, '*');
  });

  return state;
};

export default getData;
