export interface IPublicProps {
  dashboardId: string;
  chartId?: string;
  authKey?: string;
  authToken?: string;
  language?: string;
  screenmode?: ScreenMode;
  switchScreenModeOnResize?: boolean;
  loaderBackground?: string;
  loaderFontColor?: string;
  loaderSpinnerColor?: string;
  loaderSpinnerBackground?: string;
  loaderLogoColor?: string;
  debug?: boolean;
}

export interface ICssStyles {
  loaderSize: number;
  borderWidth: number;
  minHeight: number;
}

export enum ScreenMode {
  MOBILE = 'mobile',
  TABLET = 'tablet',
  DESKTOP = 'desktop',
  LARGE_SCREEN = 'largeScreen',
  FIXED = 'fixed',
  AUTO = 'auto',
}

export interface IError {
  message?: string;
  displayableMessage?: string;
}

export const breakpoints = {
  [ScreenMode.MOBILE]: [150, 767],
  [ScreenMode.TABLET]: [768, 1199],
  [ScreenMode.DESKTOP]: [1200, 1599],
  [ScreenMode.LARGE_SCREEN]: [1600, 9999],
  [ScreenMode.FIXED]: [],
};

export enum MessageTypes {
  INIT = 'init',
  CHANGED_FILTERS = 'changedFilters',
  DATA = 'data',
  LOAD = 'load',
  ERROR = 'error',
  EXPORT = 'export',
}

export interface IDashboardFilterParameter {
  column_id: string;
  dataset_id: string;
}

export interface IDashboardFilter {
  parameters?: IDashboardFilterParameter[];
  expression?: string;
  column_id?: string;
  value?: string;
}

export interface IDashboardParsedFilter {
  expression: string;
  columnId: string;
  value: string;
}

export interface IDashboardFilters {
  dashboardId: string;
  activeFilters: IDashboardParsedFilter[];
}

export interface IAvailableScreenmode {
  width: number[];
  height: number[];
  mode: string;
}

